*,
*::before,
*::after {
  box-sizing: border-box;
  text-decoration: none;
}

:root {
  --color-primary: #2584ff;
  --color-secondary: #00d9ff;
  --color-accent: #ff3400;
  --color-heading: #1b0760;
  --color-body: #918ca4;
}

/* Typography */

html {
  /* 62.5% * 16px = 10px */
  font-size: 62.5%;
}

body {
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-size: 2.4rem;
  /* 1.5 times the current font size that is 2.4 rem that is 24px * 1.5 = 36px */
  line-height: 1.5;
  color: var(--color-body);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--color-heading);
  margin-bottom: 1rem;
}

h1 {
  font-size: 7rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

p {
  margin-top: 0;
}

@media screen and (min-width: 1024px) {
  .link-arrow {
    font-size: 1.5rem;
  }

  body {
    font-size: 1.8rem;
  }

  h1 {
    font-size: 8rem;
  }

  h2 {
    font-size: 4rem;
  }

  h3 {
    font-size: 2.4rem;
  }
}

/* Links */

.link-arrow {
  color: var(--color-accent);
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: bold;
} 

.link-arrow::after {
  content: "-->";
  margin-left: 5px;
  transition: margin 0.15s;
}

.link-arrow:hover::after {
  margin-left: 10px;
}

@media screen and (min-width: 1024px) {
  .link-arrow {
    font-size: 1.5rem;
  }
}

/* Badges */

.badge {
  border-radius: 20px;
  font-size: 2rem;
  font-weight: 700;
  padding: 0.5rem 2rem;
  white-space: nowrap;
  font-size: 2rem;

}

.badge--primary{
  background: var(--color-primary);
  color:#fff;
}

.badge--secondary{
  background: var(--color-secondary);
  color:#fff;
}

.badge--small{
  font-size: 1.6rem;
}

@media screen and (min-width:1024px){
.badge{
  font-size: 1.5rem;
}

.badge--small{
  font-size: 1.2rem;
}

}


/* Lists */
.list{
  list-style: none;
  padding-left: 0;
  color: var(--color-heading);
}

.list--inline .list__item{
  display: inline-block;
  margin-right: 2rem;
}

.list--tick{
  list-style-image: url(../images/tick.svg);
  padding-left: 3rem;
}

.list--tick .list__item{
  padding-left: 0.5rem;
}


@media screen and (min-width:1024px){
  .list--tick .list__item{
    padding: 0;
  }
}

/* Icons */

.icon-container{
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: #f3f9fa;
  width: 64px;
  height: 64px;
  border-radius: 100%;

}

.icon{
  width: 40px;
  height: 40px;
}

.icon--primary{
  fill: var(--color-primary); 
}